.App {
  background: linear-gradient(to right, #16335d, #1f498a);
  height: 100vh;
  overflow: auto;

}
*{
  color: white;
  font-family: "Poppins";
}

.App-logo {
  width: 20%;
  pointer-events: none;
}


.App-header {
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-body{
  display: flex;
  margin-top: 100px;
  height: 100vh;
}

.headline{
  text-align: start;
  margin-left: 5%;
  flex: 1;
}

.headline h2{
  font-weight: normal;
  font-style: italic;
  font-size: 60px;
  line-height: 1.3;
}
.headline b{
  color: yellow;
}

.headline b{
  color: yellow;
}

#caption{
  font-weight: normal;
  font-size: 25px;
  width: 65%;
}

#cek-button{
  color: yellow;
  font-size: 30px;
}

#woman_photo{
  width: 40%;
  object-fit: contain;
}

.App-about{
  height: 100vh;
  align-items: center;
  display: flex;
  flex-flow: column;
  gap: 10px;
}

.App-about h2{
  font-size: 50px;
  color: yellow;
  margin-bottom: 0; 
}

.App-about p{
  width: 80%;
  font-size: 25px;
  margin-top: 0;
}

.foto-murid{
  display: flex;
  margin: 0;
  justify-content: space-between;
}

.foto-murid img{
  margin: 0;
  height: auto;
  object-fit: contain;
}

.foto-murid-polaroid{
  display: flex;
}

#foto4{
  width: 20%;
}
#foto2 {
  width: 25%;
}

#foto1{
  position: relative;
  width: 60%;
  transform: translateX(-40%);
}

#foto3{
  width: 70%;
  width: 60%;
  transform: translateX(10%);
}



.App-reason{
  margin-top: 10%;
  height: 100vh;
  text-align: cen;
}